'use client';

import { useState } from 'react';
import { useAdminDashboard, CreateDoctorData } from '@/hooks/useAdminDashboard';
import { StatCard } from '@/components/admin/StatCard';
import CreateDoctorModal from '@/components/admin/CreateDoctorModal';
import { Plus, AlertCircle, Stethoscope, Users, Calendar, Activity } from 'lucide-react';
import { format } from 'date-fns';
import { tr } from 'date-fns/locale';

export default function AdminDashboardPage() {
  // 1. Veri ve Mantƒ±ƒüƒ± Hook'tan alƒ±yoruz
  const { doctors, stats, isLoading, error, refreshData, addDoctor } = useAdminDashboard();
  
  // 2. Sadece Modalƒ±n a√ßƒ±k/kapalƒ± durumunu burada tutuyoruz
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);

  // 3. Olay Y√∂netimi (Handler)
  const handleCreateDoctor = async (data: CreateDoctorData) => {
    const result = await addDoctor(data);
    
    if (result.success) {
      setIsCreateModalOpen(false); // Ba≈üarƒ±lƒ±ysa kapat
      alert("‚úÖ Doktor ba≈üarƒ±yla sisteme eklendi!");
    } else {
      alert("‚ùå Hata: " + result.message);
    }
  };

  if (isLoading) return <div className="p-10 flex justify-center text-gray-400 font-medium">Panel Y√ºkleniyor...</div>;

  return (
    <div className="p-8 max-w-7xl mx-auto">
      {/* Ba≈ülƒ±k ve Buton */}
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-10 gap-4">
        <div>
          <h1 className="text-3xl font-extrabold text-gray-900 tracking-tight">Genel Bakƒ±≈ü</h1>
          <p className="text-gray-500 text-sm mt-1">Klinik performansƒ±nƒ± ve ekibini buradan y√∂netebilirsin.</p>
        </div>
        <button 
          onClick={() => setIsCreateModalOpen(true)}
          className="flex items-center gap-2 bg-red-600 hover:bg-red-700 text-white px-5 py-2.5 rounded-xl font-bold transition-all shadow-lg shadow-red-200"
        >
          <Plus size={20} />
          Yeni Doktor Ekle
        </button>
      </div>

      {/* Hata Mesajƒ± */}
      {error && (
        <div className="mb-6 flex items-center gap-3 p-4 bg-red-50 border border-red-100 text-red-700 rounded-xl animate-in fade-in">
          <AlertCircle size={20} />
          <p className="font-medium">Hata: {error}</p>
          <button onClick={refreshData} className="ml-auto underline text-xs">Tekrar Dene</button>
        </div>
      )}

      {/* ƒ∞statistikler */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mb-10">
        <StatCard title="Toplam Doktor" value={stats.totalDoctors} icon={Stethoscope} color="text-blue-600" bg="bg-blue-50" />
        <StatCard title="Kayƒ±tlƒ± Hasta" value={stats.totalPatients} icon={Users} color="text-green-600" bg="bg-green-50" />
        <StatCard title="Aylƒ±k Randevu" value={stats.totalAppointments} icon={Calendar} color="text-purple-600" bg="bg-purple-50" />
        <StatCard title="Sistem Saƒülƒ±ƒüƒ±" value={`%${stats.systemHealth}`} icon={Activity} color="text-red-600" bg="bg-red-50" />
      </div>

      {/* Doktor Listesi */}
      <div className="bg-white rounded-2xl shadow-sm border border-gray-100 overflow-hidden">
        <div className="p-6 border-b border-gray-50">
          <h2 className="text-lg font-bold text-gray-800">Doktor Kadrosu</h2>
        </div>
        <div className="overflow-x-auto">
          <table className="w-full text-left">
            <thead className="bg-gray-50/50 text-gray-400 text-[11px] uppercase tracking-widest font-bold">
              <tr>
                <th className="px-6 py-4">Doktor</th>
                <th className="px-6 py-4">Kayƒ±t Tarihi</th>
                <th className="px-6 py-4">Durum</th>
                <th className="px-6 py-4 text-right">Aksiyon</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-50">
              {doctors.length > 0 ? doctors.map((doc) => (
                <tr key={doc.id} className="hover:bg-gray-50/50 transition-colors">
                  <td className="px-6 py-4">
                    <div className="flex items-center gap-3">
                      <div className="w-9 h-9 rounded-full bg-red-50 text-red-600 flex items-center justify-center font-bold text-xs uppercase">
                        {doc.firstName?.[0]}{doc.lastName?.[0]}
                      </div>
                      <div>
                        <p className="text-sm font-bold text-gray-800">{doc.firstName} {doc.lastName}</p>
                        <p className="text-xs text-gray-500">{doc.email}</p>
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 text-sm text-gray-600">
                    {format(new Date(doc.createdAt), 'dd MMM yyyy', { locale: tr })}
                  </td>
                  <td className="px-6 py-4">
                    <span className="px-2.5 py-1 text-[10px] font-bold bg-green-100 text-green-700 rounded-lg uppercase">Aktif</span>
                  </td>
                  <td className="px-6 py-4 text-right">
                    <span className="text-xs text-gray-400">D√ºzenle / Sil</span>
                  </td>
                </tr>
              )) : (
                <tr><td colSpan={4} className="px-6 py-12 text-center text-gray-400">Hen√ºz doktor eklenmemi≈ü.</td></tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Modal Bile≈üeni - Sayfanƒ±n en altƒ±na, t√ºm i√ßerikten baƒüƒ±msƒ±z eklenir */}
      <CreateDoctorModal 
        isOpen={isCreateModalOpen} 
        onClose={() => setIsCreateModalOpen(false)} 
        onSave={handleCreateDoctor} 
      />
    </div>
  );
}